'use client';

import { useState } from 'react';
import { Button } from '@/components/ui/button';
import { Card, CardContent, CardDescription, CardHeader, CardTitle, CardFooter } from '@/components/ui/card';
import { Input } from '@/components/ui/input';
import { Label } from '@/components/ui/label';
import { useToast } from '@/hooks/use-toast';
import { AlertCircle, PlusCircle, User, Pill, Hash, Stethoscope, BriefcaseMedical, RefreshCw, Loader2, Search, Trash2 } from 'lucide-react';
import { Accordion, AccordionContent, AccordionItem, AccordionTrigger } from '@/components/ui/accordion';
import { Badge } from '@/components/ui/badge';
import { checkInteractions } from '@/app/actions';
import { type DetectDrugInteractionsOutput } from '@/ai/flows/detect-drug-interactions';
import { Separator } from '@/components/ui/separator';
import { Tabs, TabsContent, TabsList, TabsTrigger } from '@/components/ui/tabs';
import { PrescriptionUpload } from './prescription-upload';

interface Medication {
  id: number;
  name: string;
  rxNumber: string;
  doctor: string;
  pills: number;
  dosage: string;
  refillsLeft: number;
  frequency: string;
  form: string;
}

interface Patient {
    name: string;
    membershipNumber: string;
    medications: Medication[];
}

const mockPatientData: Patient = {
  name: 'Jane Doe',
  membershipNumber: 'MEM123456789',
  medications: [
    { id: 1, name: 'Lisinopril', rxNumber: 'RX789123', doctor: 'Dr. Smith', pills: 30, dosage: '10mg', refillsLeft: 2, frequency: 'Once daily', form: 'Tablet' },
    { id: 2, name: 'Metformin', rxNumber: 'RX456789', doctor: 'Dr. Smith', pills: 60, dosage: '500mg', refillsLeft: 1, frequency: 'Twice daily', form: 'Tablet' },
    { id: 3, name: 'Simvastatin', rxNumber: 'RX123456', doctor: 'Dr. Jones', pills: 30, dosage: '20mg', refillsLeft: 3, frequency: 'Once daily', form: 'Tablet' },
  ]
};

export function ProfessionalDashboard() {
  const [membershipNumber, setMembershipNumber] = useState('');
  const [patient, setPatient] = useState<Patient | null>(null);
  const [isLoading, setIsLoading] = useState(false);
  const [isCheckingInteractions, setIsCheckingInteractions] = useState(false);
  const [newDrugName, setNewDrugName] = useState('');
  const [interactions, setInteractions] = useState<DetectDrugInteractionsOutput['interactions']>([]);
  const { toast } = useToast();

  const handleFindPatient = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!membershipNumber) {
        toast({ variant: 'destructive', title: 'Error', description: 'Please enter a membership number.' });
        return;
    }
    setIsLoading(true);
    // Simulate API call to fetch patient data
    await new Promise(resolve => setTimeout(resolve, 1000));
    if (membershipNumber === mockPatientData.membershipNumber) {
        setPatient(mockPatientData);
        toast({ title: 'Patient Found', description: `Displaying medication history for ${mockPatientData.name}.` });
        runInteractionCheck(mockPatientData.medications.map(m => m.name));
    } else {
        toast({ variant: 'destructive', title: 'Patient Not Found', description: 'No patient found with that membership number.' });
        setPatient(null);
    }
    setIsLoading(false);
  };

  const runInteractionCheck = async (drugList: string[]) => {
    if (drugList.length < 2) {
      setInteractions([]);
      return;
    }
    setIsCheckingInteractions(true);
    const response = await checkInteractions(drugList);
    if (response && 'error' in response) {
      toast({ variant: 'destructive', title: 'Error', description: response.error });
      setInteractions([]);
    } else {
      setInteractions(response.interactions || []);
      if (response.interactions && response.interactions.length > 0) {
        toast({ variant: 'destructive', title: 'Interaction Alert', description: 'A potential drug interaction was detected. Please review the medications.' });
      }
    }
    setIsCheckingInteractions(false);
  };
  
  const handleAddDrug = async (e: React.FormEvent) => {
    e.preventDefault();
    if (!newDrugName || !patient) {
      toast({ variant: 'destructive', title: 'Error', description: 'Please enter a drug name.' });
      return;
    }
    const newMed: Medication = {
      id: Date.now(),
      name: newDrugName,
      rxNumber: `RX${Date.now().toString().slice(-6)}`,
      doctor: 'Dr. Current Professional',
      pills: 30, // Default value
      dosage: 'N/A',
      refillsLeft: 0,
      frequency: 'N/A',
      form: 'Unknown'
    };
    
    const updatedMeds = [...patient.medications, newMed];
    setPatient({ ...patient, medications: updatedMeds });
    setNewDrugName('');
    
    await runInteractionCheck(updatedMeds.map(m => m.name));
  };

  const removeDrug = (id: number) => {
    if (!patient) return;
    const updatedMeds = patient.medications.filter(med => med.id !== id);
    setPatient({ ...patient, medications: updatedMeds });
    runInteractionCheck(updatedMeds.map(m => m.name));
  };

  const handleDrugsParsed = (drugList: string[]) => {
    if (!patient) return;
    if (drugList.length === 0) {
      toast({ variant: 'destructive', title: 'No Drugs Found', description: "We couldn't find any medication names in the image." });
      return;
    }

    const newMeds: Medication[] = drugList.map(name => ({
      id: Date.now() + Math.random(),
      name,
      rxNumber: `RX${Date.now().toString().slice(-6)}${Math.floor(Math.random() * 100)}`,
      doctor: 'Dr. Current Professional',
      pills: 30,
      dosage: 'N/A',
      refillsLeft: 0,
      frequency: 'N/A',
      form: 'Unknown',
    }));

    const updatedMeds = [...patient.medications, ...newMeds];
    setPatient({ ...patient, medications: updatedMeds });
    runInteractionCheck(updatedMeds.map(m => m.name));
    toast({ title: 'Medications Added', description: `${newMeds.length} medication(s) added to ${patient.name}'s file.` });
  };


  const getInteractionStatus = (drugName: string) => {
    return interactions.some(int => int.drugA.toLowerCase() === drugName.toLowerCase() || int.drugB.toLowerCase() === drugName.toLowerCase());
  };

  return (
    <div className="space-y-6">
        <Card>
            <CardHeader>
                <CardTitle>Patient Lookup</CardTitle>
                <CardDescription>Enter a patient's membership number to view their medication history.</CardDescription>
            </CardHeader>
            <form onSubmit={handleFindPatient}>
                <CardContent>
                    <Label htmlFor="membership-lookup" className="sr-only">Membership Number</Label>
                    <Input 
                        id="membership-lookup"
                        placeholder="Enter patient membership number"
                        value={membershipNumber}
                        onChange={(e) => setMembershipNumber(e.target.value)}
                        disabled={isLoading}
                    />
                </CardContent>
                <CardFooter>
                    <Button type="submit" disabled={isLoading}>
                        {isLoading ? <Loader2 className="animate-spin mr-2" /> : <Search className="mr-2" />}
                        Find Patient
                    </Button>
                </CardFooter>
            </form>
        </Card>

        {patient && (
             <Card>
                <CardHeader>
                    <CardTitle className="flex items-center justify-between">
                        <span>
                            Medication History for <span className="text-primary">{patient.name}</span>
                        </span>
                        {isCheckingInteractions && <Loader2 className="animate-spin text-muted-foreground" />}
                    </CardTitle>
                    <CardDescription>
                        A list of the patient's current medications. Interaction alerts will appear here.
                    </CardDescription>
                </CardHeader>
                <CardContent className="space-y-4">
                     <div className="grid grid-cols-1 md:grid-cols-2 gap-4">
                        {patient.medications.map((med) => (
                        <Card key={med.id} className={getInteractionStatus(med.name) ? 'border-destructive' : ''}>
                            <div className="p-4">
                                <div className="flex justify-between items-start">
                                    <div>
                                    <h3 className="font-bold text-lg flex items-center gap-2">
                                        <Pill size={20} /> {med.name}
                                        {getInteractionStatus(med.name) && (
                                            <Badge variant="destructive" className="flex items-center gap-1">
                                                <AlertCircle size={14} /> Interaction
                                            </Badge>
                                        )}
                                    </h3>
                                    <p className="text-sm text-muted-foreground">
                                        {med.dosage} - {med.frequency}
                                    </p>
                                    </div>
                                    <Button variant="ghost" size="icon" onClick={() => removeDrug(med.id)} className="text-muted-foreground hover:text-destructive">
                                        <Trash2 size={16} />
                                    </Button>
                                </div>
                                <Accordion type="single" collapsible className="w-full">
                                    <AccordionItem value="item-1" className="border-b-0">
                                        <AccordionTrigger className="text-sm pt-2 pb-0">View Details</AccordionTrigger>
                                        <AccordionContent className="text-muted-foreground space-y-1 pt-2 text-sm">
                                        <p className="flex items-center gap-2"><Hash size={14}/> <strong>RX Number:</strong> {med.rxNumber}</p>
                                        <p className="flex items-center gap-2"><Stethoscope size={14} /> <strong>Prescribed by:</strong> {med.doctor}</p>
                                        <p className="flex items-center gap-2"><BriefcaseMedical size={14} /> <strong>Pill Count:</strong> {med.pills}</p>
                                        <p className="flex items-center gap-2"><RefreshCw size={14} /> <strong>Refills Left:</strong> {med.refillsLeft}</p>
                                        </AccordionContent>
                                    </AccordionItem>
                                </Accordion>
                            </div>
                        </Card>
                        ))}
                    </div>

                    {patient.medications.length === 0 && (
                        <div className="text-center py-8 text-muted-foreground border-2 border-dashed rounded-lg">
                            This patient has no medications on file.
                        </div>
                    )}
                    
                    <Separator className="my-6"/>

                    <div>
                        <h4 className="font-semibold text-lg mb-2">Add New Medication</h4>
                        <p className="text-sm text-muted-foreground mb-4">Add a new drug to check for interactions against the patient's current list.</p>
                        <Tabs defaultValue="manual">
                            <TabsList className="grid w-full grid-cols-2">
                                <TabsTrigger value="manual">Manual Entry</TabsTrigger>
                                <TabsTrigger value="upload">Upload Prescription</TabsTrigger>
                            </TabsList>
                            <TabsContent value="manual" className="pt-4">
                                <form onSubmit={handleAddDrug} className="flex items-center gap-2">
                                    <Input
                                        placeholder="e.g., Ibuprofen"
                                        value={newDrugName}
                                        onChange={(e) => setNewDrugName(e.target.value)}
                                        disabled={isCheckingInteractions}
                                    />
                                    <Button type="submit" disabled={isCheckingInteractions}>
                                        {isCheckingInteractions ? <Loader2 className="animate-spin mr-2" /> : <PlusCircle className="mr-2" />}
                                        Add and Check
                                    </Button>
                                </form>
                            </TabsContent>
                            <TabsContent value="upload">
                                <PrescriptionUpload onDrugsParsed={handleDrugsParsed} />
                            </TabsContent>
                        </Tabs>
                    </div>

                </CardContent>
            </Card>
        )}
    </div>
  );
}
